.popup-box__container {
  position: relative;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  min-width: 430px;
  color: #000000;
}
@media screen and (max-width: 461px) {
  .popup-box__container {
    min-width: 282px;
  }
}
