.profile__about-me {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 1.125rem;
  line-height: 1.2222;
  grid-area: 2 / 1 / 3 / 3;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 461px) {
  .profile__about-me {
    font-size: 0.875rem;
    line-height: 1.214285714285714;
  }
}
